import React, { useState, useMemo } from 'react'
import size from 'lodash/size'

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text'
import Button from '../components/Button'
import Dropdown from '../components/Dropdown'
import { responsive } from '../components/ThemeProvider/theme';

import SheetDataTable from './SheetDataTable'

import useMsal from '../contexts/msal/useMsal'
import RenderTable from './RenderTable';

const HomePage = () => {
  const {
    account,
    loadSheets,
    loading,
    sheets,
    AuthButton,
    loadSheetData,
    sheetData,
    WorkBookIdInput,
  } = useMsal()
  const [selectedSheet, setSheet] = useState()
  const [compareSheet, setCompareSheet] = useState()
  const [selectedRow, setSelectedRow] = useState({})
  const data = sheetData[selectedSheet]
  const compareSheetData = compareSheet && sheetData[compareSheet]
  const doubleHeader = useMemo(() => data && data.text && data.text[0].some(h => !h), [data])
  const headerRow = useMemo(() => {
    if (!data || !data.text) return []
    if (doubleHeader) {
      const colLength = data.text[0].length
      let groupMemo = []
      // 空白代表要group ['a', 'b', '', 'c']
      // b後面的空白要跟b group一起，所以先反轉陣列，從後面檢查回來，只要是空白，就等到下一個值出現然後把他們group起來
      return data.text[0].slice(0).reverse().reduce((all, h, i) => {
        // 檢查有沒有值
        if (h) {
          // 檢查有沒有之前先標記要變成group的
          if (groupMemo.length) {
            // if true 把現在這一格也加入group
            groupMemo.push(i)
            all.push({
              Header: h,
              columns: groupMemo.slice().reverse().map(j => ({
                Header: data.text[1][colLength - j - 1],
                accessor: d => d[colLength - j - 1],
                id: `${i}-${j}`,
              })),
            })
            // 清空group的標記
            groupMemo = []
          } else {
            // if false 就是一般的header
            all.push({ Header: h, accessor: d => d[colLength - i - 1], id: i })
          }
        } else {
          // 沒有值，之後要跟別人group，先暫存起來
          groupMemo.push(i)
        }
        return all
      }, []).reverse() // 最後記得再反轉回來
    }
    return data.text[0]
  }, [data, doubleHeader])
  const tableData = useMemo(() => (data && data.text) ? data.text.slice(doubleHeader ? 2 : 1) : [], [data, doubleHeader])
  const compareData = useMemo(() => (compareSheetData && compareSheetData.text) ? compareSheetData.text.slice(doubleHeader ? 2 : 1) : [], [compareSheetData, doubleHeader])
  const indexedData = useMemo(() => (data && data.text) ? data.text.reduce((all, d, i) => {
    const offset = doubleHeader ? 2 : 1
    if (i >= offset) all[i - offset] = d
    return all
  }, {}) : {}, [data, doubleHeader])

  const renderValues = useMemo(() => {
    if (!size(selectedRow)) return []
    return Object.entries(selectedRow).map(([i, selected]) => selected ? indexedData[i] : null).filter(Boolean)
  }, [selectedRow, indexedData])

  const updatedMark = useMemo(() => {
    if (!size(compareData)) return null
    const compareMark = compareData.reduce((all, row) => {
      all[row[0]] = row
      return all
    }, {})
    return renderValues.map((row) => {
      const toCompare = compareMark[row[0]]
      if (toCompare) {
        return row.map((r, i) => r !== toCompare[i])
      } else {
        return row.fill(true)
      }
    })

  }, [renderValues, compareData])

  return (
    <Container py={responsive(0, '2em')}>
      {account ? (
        <Box>
          <Flex>
            <WorkBookIdInput flex="1" />
            <Box ml="1em">
              <Button disabled={loading} my="1em" onClick={loadSheets}>載入資料表</Button>
            </Box>
          </Flex>
          {sheets && (
            <>
              <Dropdown
                label="選擇資料表"
                onChange={(selected) => {
                  if (selected) {
                    setSheet(selected.value)
                    loadSheetData(selected.value)
                  } else {
                    setSheet()
                  }
                }}
                options={sheets.map(sheet => sheet.name)}
              />
              {data && (
                <>
                  <SheetDataTable
                    data={tableData}
                    headerRow={headerRow}
                    onSelectRow={setSelectedRow}
                  />
                  <Dropdown
                    my="1em"
                    label="對比資料表"
                    onChange={(selected) => {
                      if (selected) {
                        setCompareSheet(selected.value)
                        loadSheetData(selected.value)
                      } else {
                        setCompareSheet()
                      }
                    }}
                    options={sheets.map(sheet => sheet.name).filter(name => name !== selectedSheet)}
                  />
                  {renderValues.length ? (
                    <RenderTable
                      allData={tableData}
                      data={renderValues}
                      headerRow={headerRow}
                      updatedMark={updatedMark}
                    />
                  ) : null}
                </>
              )}
            </>
          )}
        </Box>
      ) : (
        <>
          <Text>請先登入</Text>
          <AuthButton />
        </>
      )}
    </Container>
  )
}

export default HomePage
