import React, { useMemo, useRef, useEffect, useCallback } from 'react';
import { useTable } from 'react-table'
import styled from 'styled-components'
import isObject from 'lodash/isObject'
import findLastIndex from 'lodash/findLastIndex'
import { saveAs } from 'file-saver'

import '../vendor/Blob'
import '../vendor/canvas-toBlob'

import Box from '../components/Box'
import Button from '../components/Button'

import Dom2Canvas from '../utils/dom2canvas'

import bg from './bg.png'

const CanvasContainer = styled.div`
canvas {
  width: 100%;
  height: auto;
}
`

const WIDTH = 1920
const HEIGHT = 1080
const fontFamily = 'Arial'

const bgImg = new Image()
bgImg.src = bg

const RenderTable = ({ data, headerRow, allData, updatedMark }) => {
  const tableRef = useRef()
  const container = useRef()
  const columns = useMemo(() => {
    const cols = headerRow.slice(1, headerRow.length - 2)
      .concat(headerRow[0])
      .map((Header, i) => isObject(Header) ? Object.assign({}, Header) : { Header, accessor: d => d[i], id: i })
    return cols
  }, [headerRow])

  const tableInstance = useTable({
    columns,
    data,
  })

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = tableInstance

  const orginizedHeader = useMemo(() => {
    const placeHolders = {}
    return headerGroups.reduce((headers, headerGroup, i) => {
      headers[i] = headerGroup.headers.map((header, j) => {
        if (header.placeholderOf) {
          placeHolders[header.placeholderOf.id] = [i, j]
          return {}
        } else if (placeHolders[header.id]) {
          const [x, y] = placeHolders[header.id]
          headers[x][y] = { rowSpan: i - x + 1, children: header.render('Header') }
          return {}
        } else {
          return { colSpan: header.totalVisibleHeaderCount, children: header.render('Header') }
        }
      })
      return headers
    }, [])
  }, [headerGroups])

  const orginizedRows = useMemo(() => {
    return rows.reduce((allRows, row, i) => {
      prepareRow(row)
      const thisRow = row.cells.map((cell, j) => {
        if (cell.value) {
          return { children: cell.render('Cell'), rowSpan: 1, updated: updatedMark && updatedMark[i][j] }
        } else if (i === 0) {
          const parentIndex = allData.findIndex(r => r[j + 1])
          return { children: allData[parentIndex][j + 1], rowSpan: 1, updated: updatedMark && updatedMark[parentIndex][j + 1] }
        } else {
          const parentIndex = findLastIndex(allRows, r => r[j].rowSpan)
          if (parentIndex > -1) allRows[parentIndex][j].rowSpan += 1
          return {}
        }
      })
      return allRows.concat([thisRow])
    }, [])
  }, [rows, prepareRow, allData, updatedMark])

  const handleDownload = useCallback(() => {
    container.current.querySelector('canvas').toBlob((blob) => {
      saveAs(blob, 'image.png');
    });
  }, [container])

  useEffect(() => {
    const ins = new Dom2Canvas(tableRef.current, WIDTH, HEIGHT, bgImg)
    container.current.innerHTML = ''
    ins.toCanvas(canvas => {
      // presentational style
      canvas.style.width = '100%'
      canvas.style.height = 'auto'
      if (container.current) container.current.appendChild(canvas)
    })
  }, [data, updatedMark])

  return (
    <Box.Relative my="2em">
      <CanvasContainer ref={container} />
      <Button my="0.5em" onClick={handleDownload}>下載圖檔</Button>
      <Box.Absolute
        width={1}
        height={1}
        opacity={0}
        overflow="hidden"
      >
        <Box.Relative width={WIDTH} height={HEIGHT} ref={tableRef}>
          <table
            style={{
              position: 'absolute',
              top: 72 * 2,
              left: 32 * 2,
              right: 48 * 2,
              bottom: 85 * 2,
            }}
            {...getTableProps()}
          >
            <thead>
              {headerGroups.map((headerGroup, i) => (
                <tr {...headerGroup.getHeaderGroupProps()}>
                  {// Loop over the headers in each row
                  headerGroup.headers.map((column, j) => {
                    const { children, ...rest } = orginizedHeader[i][j]
                    return children ? (
                      <th
                        {...column.getHeaderProps()}
                        {...rest}
                        style={{
                          background: '#0E4E6E',
                          color: 'white',
                          padding: '0.5em',
                          fontFamily,
                          fontWeight: 'bold',
                          fontSize: '24pt',
                        }}>
                        <span
                          style={{
                            writingMode: 'vertical-rl',
                            textOrientation: 'mixed',
                            transform: 'rotate(180deg)',
                            whiteSpace: 'pre',
                          }}
                        >
                          {children}
                        </span>
                      </th>
                    ) : null
                  })}
                </tr>
              ))}
            </thead>
            {/* Apply the table body props */}
            <tbody {...getTableBodyProps()}>
              {// Loop over the table rows
              rows.map((row, i) => {
                // Prepare the row for display
                return (
                  // Apply the row props
                  <tr {...row.getRowProps()}>
                    {// Loop over the rows cells
                    row.cells.map((cell, j) => {
                      // Apply the cell props
                      const { children, updated, ...rest } = orginizedRows[i][j]
                      return children ? (
                        <td
                          {...cell.getCellProps()}
                          {...rest}
                          style={{
                            fontFamily,
                            fontSize: '20pt',
                            padding: '0.5em',
                            whiteSpace: 'pre',
                            textAlign: 'center',
                            background: '#E1E1E1',
                            color: updated ? '#D6262F' : 'black',
                          }}
                        >
                          {children}
                        </td>
                      ) : null
                    })}
                  </tr>
                )
              })}
            </tbody>
          </table>
        </Box.Relative>
      </Box.Absolute>
    </Box.Relative>
  )
};

export default RenderTable;
