import { useEffect } from 'react'
import useLocalStorage from 'react-use-localstorage';

export default (name, initialVal, devOnly) => hook => {
  if (devOnly && process.env.NODE_ENV !== 'development') {
    return hook(initialVal)
  }
  const [item, setItem] = useLocalStorage(name, JSON.stringify(initialVal));
  const [val, setVal] = hook(item && JSON.parse(item))

  useEffect(() => {
    setItem(JSON.stringify(val))
  }, [val])

  return [val, setVal]
}
