import React, { useContext, useCallback } from 'react'

import Button from '../../components/Button'

import context from './context'

export default () => {
  const { account, signIn, signOut, ...rest } = useContext(context)
  const AuthButton = useCallback(() => (
    <Button onClick={account ? signOut : signIn}>{account ? 'Logout' : 'Login'}</Button>
  ), [account])
  return {
    AuthButton,
    account,
    signIn,
    signOut,
    ...rest,
  }
}
