import React, { useCallback, useState, useMemo } from 'react';
import * as Msal from 'msal'
import { useImmer } from 'use-immer'

import hookWithLocalStorage from '../../utils/hookWithLocalStorage'
import Input from '../../components/Input'

import context from './context'

// Config object to be passed to Msal on creation.
// For a full list of msal.js configuration parameters,
// visit https://azuread.github.io/microsoft-authentication-library-for-js/docs/msal/modules/_authenticationparameters_.html
const msalConfig = {
  auth: {
    clientId: '5ed0f7c5-cd47-4779-bfd3-53f5b0445c94',
    authority: 'https://login.microsoftonline.com/common',
    redirectUri: process.env.NODE_ENV === 'development' ? 'http://localhost:3000/' : 'https://relabcc.github.io/winbond-excel-roadmap/',
  },
  cache: {
    cacheLocation: 'sessionStorage', // This configures where your cache will be stored
    storeAuthStateInCookie: false, // Set this to 'true' if you are having issues on IE11 or Edge
  },
};

const graphConfig = {
  getReadSheetEndPoint: id => `https://graph.microsoft.com/v1.0/me/drive/items/${id}/workbook/worksheets`,
  getReadSheetDataEndPoint: (id, sheetName) => `https://graph.microsoft.com/v1.0/me/drive/items/${id}/workbook/worksheets/${sheetName}/usedRange`,
}

// Add here scopes for id token to be used at MS Identity Platform endpoints.
const loginRequest = {
  scopes: ['openid', 'profile', 'User.Read', 'Files.Read']
};

const myMSALObj = new Msal.UserAgentApplication(msalConfig);

function callMSGraph(endpoint, token) {
  const headers = new Headers();
  const bearer = `Bearer ${token}`;

  headers.append("Authorization", bearer);

  const options = {
      method: "GET",
      headers: headers
  };

  console.log('request made to Graph API at: ' + new Date().toString());

  return fetch(endpoint, options)
    .then(response => response.json())
    .catch(error => console.log(error))
}

function getTokenPopup(request) {
  return myMSALObj.acquireTokenSilent(request)
    .catch(error => {
      console.log(error);
      console.log("silent token acquisition fails. acquiring token using popup");

      // fallback to interaction when silent call fails
        return myMSALObj.acquireTokenPopup(request)
          .then(tokenResponse => {
            return tokenResponse;
          }).catch(error => {
            console.log(error);
          });
    });
}

// https://onedrive.live.com/edit.aspx?cid=0c338d6b32e0967e&page=view&resid=C338D6B32E0967E!107&parId=C338D6B32E0967E!101&app=Excel

const Provider = ({ children }) => {
  const [account, setAccount] = useState(myMSALObj.getAccount())
  const [loading, setLoading] = useState()
  const [workbookUrl, setWorkbookUrl] = hookWithLocalStorage('WORKBOOK_URL')(useState)
  const [sheets, setSheets] = hookWithLocalStorage('WORKSHEETS')(useState)
  const [sheetData, setSheetData] = hookWithLocalStorage('SHEET_DATA', {})(useImmer)
  const workbookId = useMemo(() => {
    try {
      const url = new URL(workbookUrl);
      const params = new URLSearchParams(url.search);
      return params.get('resid')
    } catch (e) {
      return ''
    }
  }, [workbookUrl])
  const signIn = useCallback(() => {
    myMSALObj.loginPopup(loginRequest)
      .then(loginResponse => {
        console.log('id_token acquired at: ' + new Date().toString());
        console.log(loginResponse);

        if (myMSALObj.getAccount()) {
          setAccount(myMSALObj.getAccount())
        }
      }).catch(error => {
        console.log(error);
      });
  }, [setAccount])

  const signOut = useCallback(() => {
    if (account) {
      myMSALObj.logout();
      setAccount(null)
    }
  }, [setAccount, account])

  const loadSheets = () => {
    if (account && !loading && workbookId) {
      console.log('start load sheet')
      setLoading(true)
      getTokenPopup(loginRequest)
        .then(response => {
          callMSGraph(graphConfig.getReadSheetEndPoint(workbookId), response.accessToken)
            .then(data => {
              if (data.value) {
                setSheets(data.value)
              }
              setLoading(false)
            });
        }).catch(error => {
          console.log(error);
        });
    }
  }

  const loadSheetData = useCallback((sheetName) => {
    if (account && !loading && workbookId && !sheetData[sheetName]) {
      console.log('start load sheet data')
      setLoading(true)
      getTokenPopup(loginRequest)
        .then(response => {
          callMSGraph(graphConfig.getReadSheetDataEndPoint(workbookId, sheetName), response.accessToken)
            .then(data => {
              console.log(data)
              if (data) {
                setSheetData(draft => {
                  draft[sheetName] = data
                })
              }
              setLoading(false)
            });
        }).catch(error => {
          console.log(error);
        });
    }
  }, [loading, account, sheetData, workbookId])

  const WorkBookIdInput = useCallback((p) => (
    <Input
      label="貼上Excel連結"
      value={workbookUrl}
      onChange={e => setWorkbookUrl(e.target.value)}
      onKeyUp={e => e.keyCode === 13 && loadSheets()}
      {...p}
    />
  ), [workbookUrl])

  return (
    <context.Provider
      value={{
        account,
        signIn,
        signOut,
        loadSheets,
        loadSheetData,
        loading,
        sheets,
        sheetData,
        WorkBookIdInput,
      }}
    >
      {children}
    </context.Provider>
  );
};

export default Provider;
